import ProductCollection from '@app/react/common/collection/ProductCollection'
import { CollectionId } from '@app/types/collection'
import ProductCollectionLoader from '@app/react/common/collection/ProductCollectionLoader'
import { PRODUCT_LIMIT } from '@app/services/collections/constants'
import { useCollectionQuery } from '@app/services/collections/hooks'

interface HomepageCollectionProps {
  id: CollectionId
}

const HomepageCollection = ({ id }: HomepageCollectionProps) => {
  const { data, isLoading } = useCollectionQuery(id, PRODUCT_LIMIT, { enabled: !!id })

  if (isLoading) return <ProductCollectionLoader />

  if (!data?.items?.length) return null

  return (
    <div data-test={`homepage-collection-${data.id}`}>
      <ProductCollection collection={data} enableViewMore />
    </div>
  )
}

export default HomepageCollection
