import { FC } from 'react'
import { createStyles } from '@app/react/common/hooks/createStyles'
import { useIsMobile } from '@app/react/common/hooks/useIsMobile'
import { spacing } from '@instacart/ids-core'

const getStyles = createStyles(() => ({
  bannerContainerMobile: {
    marginBottom: spacing.s16,
    width: '100%',
  },
}))

// only used on mobile
const BannerLayout: FC<unknown> = ({ children }) => {
  const isMobile = useIsMobile()
  const styles = getStyles()

  return <div css={[isMobile && styles.bannerContainerMobile]}>{children}</div>
}

export default BannerLayout
