import { memo, useEffect, useState } from 'react'
import { spacing } from '@instacart/ids-core'
import { createStyles } from '@app/react/common/hooks/createStyles'
import { List, ListItem, ListItemAdornment, ListItemText } from '@instacart/cocktail'
import {
  auth as authService,
  cart as cartService,
  lang as langService,
} from '@instacart/enterprise-services'
import { MobileHomeLinkV2 } from '@app/types/config'
import { useLang, useConfig } from '@app/services/config/hooks'
import { eventEmitter, EVENT_TYPES } from '@app/utils/eventEmitter'
import createPlatformUrl from '@app/utils/createPlatformUrl'
import { VariantStyleType } from './types'

const useStyles = createStyles(theme => ({
  cartBadge: {
    background: theme.colors.brandHighlightRegular,
  },
  chev: {
    fontSize: theme.typography.bodyMedium2.fontSize,
  },
  countBadge: {
    alignSelf: 'center',
    borderRadius: theme.radius.r4,
    color: theme.colors.systemGrayscale00,
    fontSize: theme.typography.bodyMedium2.fontSize,
    padding: `${spacing.s4}px ${spacing.s8}px`,
  },
  iconAdornment: {
    flex: `0 0 ${spacing.s32}px`,
  },
  list: {
    padding: 0,
  },
  listItem: {
    color: '#000',
    paddingBottom: spacing.s8,
    paddingRight: spacing.s8,
    paddingTop: spacing.s8,
    textDecoration: 'none',
  },
  listItemLink: {
    color: 'inherit',
    display: 'flex',
    width: '100%',
  },
  loyaltyBadge: {
    background: theme.colors.brandPrimaryRegular,
  },
  variants: {
    default: {
      listItem: {
        borderBottom: `1px solid ${theme.colors.systemGrayscale20}`,
      },
    },
    pills: {
      chev: {
        color: theme.colors.systemGrayscale50,
      },
      icon: {
        color: theme.colors.systemGrayscale50,
      },
      list: {
        marginTop: spacing.s8,
        padding: `0 ${spacing.s16}px`,
      },
      listItem: {
        background: theme.colors.systemGrayscale10,
        border: `1px solid ${theme.colors.systemGrayscale20}`,
        marginBottom: spacing.s4,
      },
      listItemText: {
        fontWeight: theme.typography.titleMedium.fontWeight,
      },
    },
  },
}))

const MobileListNav = () => {
  const [cartCount, setCartCount] = useState<number>(cartService.selectors.countItems())
  const [loyaltyPoints, setLoyaltyPoints] = useState<number>(
    authService.selectors.getPrimaryLoyaltyCardPoints() || 0
  )

  useEffect(() => {
    const unsubscribe = cartService.store.subscribe(() => {
      setCartCount(cartService.selectors.countItems())
    })
    return unsubscribe
  }, [])

  useEffect(() => {
    const unsubscribe = authService.store.subscribe(() => {
      setLoyaltyPoints(authService.selectors.getPrimaryLoyaltyCardPoints() || 0)
    })
    return unsubscribe
  }, [])

  const onItemClick = (link: MobileHomeLinkV2) => {
    if (link.event === 'loyalty') {
      eventEmitter.emit(EVENT_TYPES.OPEN_LOYALTY_CONTEXT)
    } else if (link.event === 'openSideCart') {
      eventEmitter.emit(EVENT_TYPES.NAV_SIDE_CART_OPEN)
    }
  }

  const styles = useStyles()
  const config = useConfig()
  const lang = useLang()

  const { mobileHomeLinksv2 } = config
  const { intent = config.defaultShoppingContext } = authService.selectors.getUserContext()
  const configVariant = mobileHomeLinksv2.variant

  const variantStyle: VariantStyleType = styles.variants[configVariant] || {}

  return (
    <List css={[styles.list, variantStyle.list]} data-test="mobile-list-nav">
      {mobileHomeLinksv2.items.map(link => {
        const renderListItemContent = (
          <>
            {mobileHomeLinksv2.icons && link.iconCssClass && (
              <ListItemAdornment css={styles.iconAdornment}>
                <i
                  className={link.iconCssClass}
                  css={variantStyle.icon}
                  data-test="mobile-list-nav-icon"
                  aria-hidden="true"
                />
              </ListItemAdornment>
            )}

            <ListItemText css={variantStyle.listItemText}>
              {langService.translate(link.label)}
            </ListItemText>

            {cartCount > 0 && link.count && (
              <span css={[styles.countBadge, styles.cartBadge]}>
                {cartCount} {cartCount > 1 ? lang.items : lang.item}
              </span>
            )}

            {loyaltyPoints > 0 && link.points && (
              <span css={[styles.countBadge, styles.loyaltyBadge]}>
                {loyaltyPoints} {loyaltyPoints > 1 ? lang.points : lang.point}
              </span>
            )}

            <ListItemAdornment css={[styles.chev, variantStyle.chev]}>
              <i className="icon-chevron-right" aria-hidden="true" />
            </ListItemAdornment>
          </>
        )

        const renderListItem =
          !link.event && link.href ? (
            <a
              href={createPlatformUrl(link.href)}
              target={link.href && link.external ? '_blank' : undefined}
              data-test={`listitem-${link.id}-link`}
              css={[styles.listItemLink]}
              rel="noreferrer"
            >
              {renderListItemContent}
            </a>
          ) : (
            renderListItemContent
          )

        // @ts-expect-error discrepancy between FulfillmentType and ShoppingContext in intent
        return link.context?.includes(intent) || !link.context ? (
          <ListItem
            css={[styles.listItem, variantStyle.listItem]}
            data-test={`listitem-${link.id}`}
            onClick={() => link.event && onItemClick(link)}
            key={link.id}
          >
            {renderListItem}
          </ListItem>
        ) : null
      })}
    </List>
  )
}

export default memo(MobileListNav)
