import { useSelector } from 'react-redux'
import { RootState } from '@app/services/rootReducer'
import {
  categoriesSelector,
  categoriesFilteredByParentSelector,
  cateringCategorySelector,
  categoriesLoadingState,
  rootCategoriesSelector,
} from './selectors'

export const useCategories = () => useSelector(categoriesSelector)

export const useRootCategories = () => useSelector(rootCategoriesSelector)

export const useCateringCategory = () => useSelector(cateringCategorySelector)

export const useCategoriesFilteredByParent = (parentId: string | undefined) =>
  useSelector((state: RootState) => categoriesFilteredByParentSelector(state, parentId))

export const useCategoriesLoadingState = () => useSelector(categoriesLoadingState)
