import Section from '@app/react/common/Section'
import { UnataFeature, UnataFeatureVariants } from '@app/types/layouts/placement'
import CustomDivElement from '@app/react/components/customDivElement/CustomDivElement'
import MobileListNav from '@app/react/components/mobileListNav/MobileListNav'
import QuickLinks from '@app/react/components/quickLinks/QuickLinks'
import WaysToShop from '../../waysToShop/WaysToShop'
import { getVariant } from '../utils'

interface UnataFeaturePlacementProps {
  unataFeature: UnataFeature
}

const UnataFeaturePlacement = ({ unataFeature }: UnataFeaturePlacementProps) => {
  return (
    <Section>
      {(() => {
        switch (getVariant(unataFeature)) {
          case UnataFeatureVariants.WaysToShop:
            return <WaysToShop {...unataFeature.waysToShop} />
          case UnataFeatureVariants.OrderHistory:
            return (
              <CustomDivElement backgroundColor="#404040" height={80}>
                Order History
              </CustomDivElement>
            ) // TODO: order history component
          case UnataFeatureVariants.QuickLinks:
            return <QuickLinks />
          case UnataFeatureVariants.ListNav:
            return <MobileListNav />
          case UnataFeatureVariants.ContextSelector:
            return (
              <CustomDivElement backgroundColor="#408080" height={80}>
                Context Selector
              </CustomDivElement>
            ) // TODO: Context Selector component
          default:
            return null
        }
      })()}
    </Section>
  )
}

export default UnataFeaturePlacement
