import isMobileApp from '@app/utils/isMobileApp'
import urlParser from '@app/utils/urlParser'
import { config as configService } from '@instacart/enterprise-services'
import { shouldUseHashHistory } from '@app/react/history'

export const escapeImageUrl = (url: string, escapeParentheses?: boolean) => {
  const encodedURL = encodeURI(url)

  if (escapeParentheses) {
    return encodedURL.replace(/(?=[()])/g, '\\')
  }

  return encodedURL.replace("'", "\\'")
}

export const getPrependHashIfNeeded = (path: string) => {
  // Prepend '#' when HTML5 mode isn't enabled in Cordova.
  if (path?.startsWith('/') && shouldUseHashHistory()) {
    return `#${path}`
  }
  return path
}

export const isLinkInternal = (url?: string) => {
  if (!url) {
    return true
  }

  const { host } = urlParser(url) ?? {}

  if (!host || host === window.location.host) {
    return true
  }

  if (isMobileApp()) {
    const config = configService.selectors.getConfig()
    if (config.env.webUrl && urlParser(config.env.webUrl).host === host) {
      return true
    }
  }

  return false
}
