import { createStyles } from '@app/react/common/hooks/createStyles'
import { ReactNode } from 'react'

export interface SectionTitleProps {
  element?: 'h1' | 'h2' | 'h3' | 'h4'
  title: string
  children?: ReactNode
}

const useStyles = createStyles(theme => ({
  heading: {},
  headingText: {
    ...theme.typography.subtitleLarge,
  },
}))

const SectionTitle = ({ element: Element = 'h3', title, children }: SectionTitleProps) => {
  const styles = useStyles()

  return (
    <Element css={styles.heading}>
      <span css={styles.headingText}>{title}</span>
      {children}
    </Element>
  )
}

export default SectionTitle
