import { createStyles, createStyleVariables } from '@app/react/common/hooks/createStyles'
import { useConfig } from '@app/services/config/hooks'
import { spacing } from '@instacart/ids-core'

import AppLink from '@app/react/common/AppLink'
import { Category } from '@app/types/category'
import { CategoryCardStyleVariables } from './types'

const styleVariables = createStyleVariables<CategoryCardStyleVariables>('categoryCard', {
  backgroundColor: 'systemGrayscale10',
  color: 'systemGrayscale70',
})

const useStyles = createStyles(
  (theme, variables) => ({
    container: {
      maxWidth: '249px',
      width: '100%',
    },
    img: {
      display: 'block',
      height: '100%',
      objectFit: 'contain',
      width: '100%',
    },
    imgHolder: {
      backgroundColor: '#FFF',
      display: 'block',
      height: '168px',
      width: '100%',
    },
    link: {
      background: theme.colors[variables.backgroundColor],
      border: `1px solid ${theme.colors[variables.backgroundColor]}`,
      borderRadius: theme.radius.r12,
      display: 'block',
      height: '230px',
      overflow: 'hidden',
      position: 'relative',
    },
    subCategoryLink: {
      '&:hover': {
        textDecoration: 'none',
      },
      color: theme.colors.systemGrayscale70,
      fontSize: theme.typography.subtitleSmall.fontSize,
      lineHeight: '22px',
      marginRight: '10px',
      textDecoration: 'underline',
    },
    subCategoryList: {
      display: 'flex',
      flexWrap: 'wrap',
      listStyleType: 'none',
      margin: 0,
      padding: '9px 18px 0',
    },
    textAlignCenter: {
      textAlign: 'center',
    },
    textAlignLeft: {
      textAlign: 'left',
    },
    textTruncate: {
      WebkitBoxOrient: 'vertical',
      WebkitLineClamp: 2,
      display: '-webkit-box',
      textOverflow: 'ellipsis',
    },
    title: {
      ...theme.typography.subtitleMedium,
      background: theme.colors.systemGrayscale10,
      color: theme.colors[variables.color],
      display: 'block',
      margin: `${spacing.s8}px`,
      overflow: 'hidden',
    },
  }),
  styleVariables
)

interface Props {
  category: Category
  showChildren?: boolean
}

const CategoryCard = ({ category, showChildren = false }: Props) => {
  const config = useConfig()
  const { name, href, children = [], id } = category
  const img = category.images.tile?.small
  const styles = useStyles()
  const textAlignStyle = config.categoryTile?.textCentered
    ? styles.textAlignCenter
    : styles.textAlignLeft

  return (
    <div css={styles.container} data-test={`category-card-${id}`}>
      <AppLink
        clickAnalytics={{ category: 'category cateory image link', label: name }}
        css={styles.link}
        title={name}
        to={href}
      >
        <span css={styles.imgHolder}>
          <img src={img} alt={name} css={styles.img} />
        </span>
        <span css={[styles.title, styles.textTruncate, textAlignStyle]}>{name}</span>
      </AppLink>
      {showChildren && children.length > 0 && (
        <ul css={styles.subCategoryList}>
          {children.map(subCategory => (
            <li key={subCategory.id} data-test={`category-card-children-${id}`}>
              <AppLink
                clickAnalytics={{
                  category: 'catalog subcategories image link',
                  label: subCategory.name,
                }}
                css={styles.subCategoryLink}
                title={subCategory.name}
                to={subCategory.href}
              >
                {subCategory.name}
              </AppLink>
            </li>
          ))}
        </ul>
      )}
    </div>
  )
}

export default CategoryCard
