import { StoreProduct } from '@app/types/product'
import { TagOverridesLocation } from '@app/types/tags'
import { useSelector } from 'react-redux'
import { productTagsSelector, tagsSelector } from './selectors'

export const useTags = (tagLocation: TagOverridesLocation) =>
  useSelector(state => tagsSelector(state, { tagLocation }))

export const useProductTags = (
  product: StoreProduct,
  tagLocation: TagOverridesLocation = 'productCell'
) => useSelector(state => productTagsSelector(state, { product, tagLocation }))
