import { captureException } from '@sentry/browser'
import { Placement } from '@app/types/placements'
import { isLinkInternal, getPrependHashIfNeeded } from '@app/services/util/utils'
import { srefToHref } from '@app/utils/routing'
import { config as configService } from '@instacart/enterprise-services'
import urlParser from '@app/utils/urlParser'
import request from '@app/utils/request'

export const postLog = async ({ href, data }: { href: string; data: Record<string, unknown> }) => {
  try {
    await request(href, {
      body: data,
      method: 'POST',
    })
  } catch (e) {
    captureException(e)
  }
}

export const logInteraction = async (placement: Placement) => {
  const { interaction } =
    // eslint-disable-next-line no-underscore-dangle
    placement._links ?? {}
  if (interaction) {
    await postLog(interaction)
  }
}

export const logImpression = async (placement: Placement) => {
  const { impression } =
    // eslint-disable-next-line no-underscore-dangle
    placement._links ?? {}
  if (impression) {
    await postLog(impression)
  }
}

export const parseContentHref = ({ target }: Placement) => {
  const { type } = target

  if (type === 'collection') {
    const { id } = target.collection ?? {}
    if (!id) {
      return {}
    }
    const href = srefToHref['main.shop.collection'].replace('{id}', id.toString())
    return { href }
  }

  if (type === 'product') {
    const { id } = target.product ?? {}
    if (!id) {
      return {}
    }
    const href = srefToHref['main.product'].replace('{id}', id)
    return { href }
  }

  if (type === 'url') {
    const config = configService.selectors.getConfig()
    const isInternalLink = isLinkInternal(target.url)

    let href = target.url
    if (isInternalLink && href) {
      const { pathname = '', search = '' } = urlParser(href)
      const url = pathname ? pathname + search : href
      href = getPrependHashIfNeeded(url)
    }

    if (config.openExternalLinksNewTab && !isInternalLink) {
      return {
        href,
        target: '_blank',
      }
    }

    return { href }
  }

  return {}
}
