import Section from '@app/react/common/Section'
import { Banner, BannerVariants } from '@app/types/layouts/placement'
import HomepageBanners from '@app/react/components/homepageBanners/HomepageBanners'
import HeroBanner from '@app/react/components/heroBanner/HeroBanner'
import CustomDivElement from '@app/react/components/customDivElement/CustomDivElement'
import { getVariant } from '../utils'

interface BannerPlacementProps {
  banners: Banner[]
}

const BannerPlacement = ({ banners }: BannerPlacementProps) => {
  return (
    <>
      {banners.map(banner => {
        switch (getVariant(banner)) {
          case BannerVariants.AsyncImageBanner:
            if (banner.asyncImageBanner?.dataQuery?.id === 'homepage_hero') {
              return <HeroBanner key={banner.asyncImageBanner?.id} />
            }

            if (banner.asyncImageBanner?.dataQuery?.id === 'homepage_banners') {
              return <HomepageBanners key={banner.asyncImageBanner?.id} />
            }

            return (
              <Section boxed key={banner.asyncImageBanner?.id}>
                {/* eslint-disable-next-line react/jsx-no-literals */}
                <CustomDivElement>Banner</CustomDivElement>
              </Section>
            )
          /* eslint-enable react/jsx-no-literals */
          default:
            return null
        }
      })}
    </>
  )
}

export default BannerPlacement
