import { spacing } from '@instacart/ids-core'
import { FC } from 'react'

export interface CollectionLoaderProps {
  accessibleLabel: string
  fullWidth?: boolean
}

// TODO move this to constant file
export const MAX_WIDTH = 1090
export const PLACEHOLDER_IMAGE_WIDTH = 172

const getStyles = (fullWidth?: boolean) =>
  ({
    wrapper: {
      margin: `0 auto ${spacing.s32}px`,
      maxWidth: fullWidth ? 'none' : `${MAX_WIDTH}px`,
      width: '100%',
    },
  } as const)

export const CollectionLoader: FC<CollectionLoaderProps> = ({
  fullWidth,
  accessibleLabel,
  children,
}) => {
  const { wrapper } = getStyles(fullWidth)

  return (
    <section style={wrapper} aria-label={accessibleLabel}>
      {children}
    </section>
  )
}
