import { FC, ComponentType, useEffect, useRef, useMemo, useState } from 'react'
import { LoadingLockupGridItem, spacing } from '@instacart/ids-core'
import useResizeObserver from 'use-resize-observer'

const getStyles = () =>
  ({
    container: {
      display: 'flex',
      overflow: 'hidden',
      position: 'relative',
      width: '100%',
    },
    itemLoaderContainer: {
      margin: spacing.s12,
      marginLeft: 0,
      marginRight: spacing.s24,
    },
  } as const)

export const DEFAULT_NUM_ITEM_LOADERS = 10

export interface CarouselLoaderProps {
  ItemLoaderComp?: ComponentType
}

export const CarouselLoader: FC<CarouselLoaderProps> = ({
  ItemLoaderComp = LoadingLockupGridItem,
}) => {
  const { container, itemLoaderContainer } = getStyles()

  const [numItemLoaders, setNumItemLoaders] = useState<number>(DEFAULT_NUM_ITEM_LOADERS)
  const { ref: containerRef, width: containerWidth } = useResizeObserver()
  const itemRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    if (containerWidth) {
      const itemWidth = itemRef.current?.offsetWidth
      if (!itemWidth) return
      setNumItemLoaders(Math.ceil(containerWidth / itemWidth))
    }
  }, [containerWidth])

  const loaderList = useMemo(
    // only re-create the array when numItemLoader is changed
    () => Array.from({ length: numItemLoaders }).map((_, key) => ({ key })),
    [numItemLoaders]
  )

  return (
    <div ref={containerRef} style={container}>
      {loaderList.map(({ key }) => (
        <div
          key={`item-loader-${key}`}
          style={itemLoaderContainer}
          ref={key === 0 ? itemRef : undefined}
        >
          <ItemLoaderComp />
        </div>
      ))}
    </div>
  )
}
