import { CardList, CardListVariants } from '@app/types/layouts/placement'
import ShopByAisle from '@app/react/components/categories/ShopByAisle'
import { getVariant } from '../utils'

interface CardListPlacementProps {
  cardList: CardList
}

const CardListPlacement = ({ cardList }: CardListPlacementProps) => {
  return (
    <>
      {(() => {
        switch (getVariant(cardList)) {
          case CardListVariants.CardListSimple:
            return <ShopByAisle />
          default:
            return null
        }
      })()}
    </>
  )
}

export default CardListPlacement
