import type { Collection, HomepageCollection } from '@app/types/collection'

export interface Layout {
  placements: Placement[]
}

export enum PlacementTypeEnum {
  Banner = 'banner',
  ItemList = 'itemList',
  CardList = 'cardList',
  UnataFeature = 'unataFeature',
}

export type PlacementType =
  | PlacementTypeEnum.Banner
  | PlacementTypeEnum.ItemList
  | PlacementTypeEnum.UnataFeature

export interface CustomHtmlElementProps {
  [key: string]: string | number | Node | undefined
}
export interface Placement {
  id: string
  type: string
  banners?: Banner[]
  itemList?: ItemList
  cardList?: CardList
  unataFeature?: UnataFeature
  // TODO - remove this field when cleaned up POC
  props?: Collection | HomepageCollection | CustomHtmlElementProps
}

export interface BuffetDataQuery {
  resource: string
  id?: string
}

export enum BannerVariants {
  Banner = 'banner',
  AsyncImageBanner = 'asyncImageBanner',
}

export interface Banner {
  [BannerVariants.AsyncImageBanner]?: AsyncImageBanner
  // TODO: Add more variants here
}

export interface AsyncImageBanner {
  id: string
  dataQuery: BuffetDataQuery
}

export enum ItemListVariants {
  ItemList = 'itemList',
}

interface DefaultItemList {
  dataQuery: BuffetDataQuery
}

export interface ItemList {
  [ItemListVariants.ItemList]?: DefaultItemList
}

export enum CardListVariants {
  CardListSimple = 'cardListSimple',
}

interface CardListSimple {
  title: string
  dataQuery: {
    resource: string
  }
}

export interface CardList {
  [CardListVariants.CardListSimple]: CardListSimple
}

export enum UnataFeatureVariants {
  WaysToShop = 'waysToShop',
  OrderHistory = 'orderHistory',
  QuickLinks = 'quickLinks',
  ListNav = 'listNav',
  ContextSelector = 'contextSelector',
}

interface WaysToShop {
  showMoreLink?: boolean
}
interface OrderHistory {}
interface QuickLinks {}
interface ListNav {}
interface ContextSelector {}

export interface UnataFeature {
  [UnataFeatureVariants.WaysToShop]?: WaysToShop
  [UnataFeatureVariants.OrderHistory]?: OrderHistory
  [UnataFeatureVariants.QuickLinks]?: QuickLinks
  [UnataFeatureVariants.ListNav]?: ListNav
  [UnataFeatureVariants.ContextSelector]?: ContextSelector
}
