import { useIsMobile } from '@app/react/common/hooks/useIsMobile'
import { useConfig } from '@app/services/config/hooks'
import { useLocation } from 'react-router-dom'
import demoDesktopLayout from '@app/react/__mocks__/demoDesktopLayout'
import demoMobileLayout from '@app/react/__mocks__/demoMobileLayout'
import demoAlternateLayout from '@app/react/__mocks__/demoAlternateLayout'
import { Layout } from '@app/types/layouts/placement'
import ComposableHomeDesktopView from './home/ComposableHomeDesktopView'

interface LayoutMapping {
  [key: string]: Layout
}

const queryLayoutMapping: LayoutMapping = {
  alternate: demoAlternateLayout,
  desktop: demoDesktopLayout,
  mobile: demoMobileLayout,
}

const HomePage = () => {
  const isMobile = useIsMobile()
  const config = useConfig()
  const { search } = useLocation()

  const defaultLayout = isMobile ? 'mobile' : 'desktop'
  const query = new URLSearchParams(search)
  const layoutType = query.get('layout') ?? defaultLayout
  const layout = queryLayoutMapping[layoutType]

  // TODO - refactor this to be at a route level in the future.
  if (!config.react.enableHomePage) {
    return null
  }

  return <ComposableHomeDesktopView layout={layout} />
}

export default HomePage
