import { Placement } from '@app/types/placements'
import Banner from './Banner'

type Props = {
  banners: Placement[]
  onBannerClick: (banner: Placement, index: number) => void
}

const Banners = ({ banners, onBannerClick }: Props) => (
  <>
    {banners.map((banner, index) => (
      <Banner banner={banner} key={banner.id} onClick={() => onBannerClick(banner, index)} />
    ))}
  </>
)

export default Banners
