import { createSelector } from 'reselect'
import { configSelector } from '@app/services/config/selectors'
import { auth as authService } from '@instacart/enterprise-services'
import sortBy from 'lodash/sortBy'

export const quickLinksConfig = createSelector(configSelector, config => ({
  hasLoyaltyPoints: config.hasLoyaltyPoints,
  quickLinks: config.quickLinks,
}))

/**
 * Construct quick links from config
 */
export const sortedQuickLinks = createSelector(
  quickLinksConfig,
  authService.selectors.getPrimaryLoyaltyCard,
  ({ quickLinks, hasLoyaltyPoints }, primaryLoyaltyCard) =>
    sortBy(
      Object.keys(quickLinks || {}).map(key => {
        const ql = { ...quickLinks[key], id: key }

        if (key === 'loyalty' && hasLoyaltyPoints) {
          if (primaryLoyaltyCard) {
            ql.iconText = `${primaryLoyaltyCard.points_balance || 0} Points`
          }
        }

        return ql
      }),
      'order'
    )
)

/**
 * Filter quick links by intent
 */
export const filteredQuickLinks = createSelector(
  sortedQuickLinks,
  authService.selectors.getUserIntent,
  (quickLinks, intent) => {
    if (intent === 'instore') {
      return quickLinks.filter(o => o.context.list)
    }
    return quickLinks.filter(o => o.context.cart)
  }
)
