import { imageTransform } from '@instacart/enterprise-services'
import { Placement } from '@app/types/placements'
import { escapeImageUrl } from '@app/services/util/utils'
import { createStyles } from '@app/react/common/hooks/createStyles'
import { useIsMobile } from '@app/react/common/hooks/useIsMobile'

const getStyles = createStyles(
  () =>
    ({
      imageDesktop: {
        height: '100%',
        objectFit: 'cover',
        objectPosition: '50% 50%',
        width: '100%',
      },
      imageMobile: {
        bottom: 0,
        left: 0,
        margin: 'auto',
        maxHeight: '100%',
        maxWidth: '100%',
        objectFit: 'cover',
        objectPosition: '50% 50%',
        position: 'absolute',
        right: 0,
        top: 0,
        width: '100%',
      },
    } as const)
)

type Props = {
  banner: Placement
}

const BannerImage = ({ banner }: Props) => {
  const isMobile = useIsMobile()
  const styles = getStyles()

  return (
    <img
      alt={banner?.media?.alt_text ?? ''}
      css={isMobile ? styles.imageMobile : styles.imageDesktop}
      src={imageTransform(escapeImageUrl(banner?.media?.image ?? ''))}
    />
  )
}

export default BannerImage
