import { useEffect, useMemo, useState } from 'react'
import { Placement } from '@app/types/placements'
import { logPlacementImpression } from '@app/services/placements/analytics'
import { useConfig } from '@app/services/config/hooks'
import { eventEmitter, EVENT_TYPES } from '@app/utils/eventEmitter'
import * as homepageBannersService from '@app/services/homepageBanners/api'
import { useIsMobile } from '@app/react/common/hooks/useIsMobile'
import Banners from './Banners'
import MobileHomepageBanners from './MobileHomepageBanners'
import BannersLayout from './BannersLayout'

const HomepageBanners = () => {
  const [banners, setBanners] = useState<Placement[]>([])
  const config = useConfig()
  const isMobile = useIsMobile()

  const slicedBanners = useMemo(() => banners.slice(0, config.numHomepageBanners), [
    banners,
    config,
  ])

  const handleClick = (banner: Placement, index: number) => {
    const category = isMobile ? 'mobile homepage banner' : 'homepage banner'
    eventEmitter.emit(EVENT_TYPES.LOG_PROMOTED_PLACEMENT, {
      location: category,
      placement: banner,
      position: index,
    })
  }

  // fetch banners via API
  useEffect(() => {
    homepageBannersService.getHomepageBanners().then(bannerContent => {
      setBanners(bannerContent.items)
    })
  }, [setBanners])

  // analytics upon rendering of banners
  useEffect(() => {
    logPlacementImpression({
      location: 'homepage',
      placements: slicedBanners,
      type: 'homepage_banners',
    })
  }, [slicedBanners])

  if (slicedBanners?.length === 0) {
    return null
  }

  if (!isMobile) {
    return (
      <BannersLayout>
        <Banners banners={slicedBanners} onBannerClick={handleClick} />
      </BannersLayout>
    )
  }

  return <MobileHomepageBanners banners={slicedBanners} handleClick={handleClick} />
}

export default HomepageBanners
