import { useSelector } from 'react-redux'
import { spacing } from '@instacart/ids-core'
import { useConfig, useLang } from '@app/services/config/hooks'
import { createStyles } from '@app/react/common/hooks/createStyles'
import { filteredQuickLinks } from './selectors'
import QuickLinkItem from './QuickLinkItem'

const useStyles = createStyles(
  () =>
    ({
      quickLinkItem: {
        display: 'inline-block',
        height: '150px',
        margin: '0 1%',
        verticalAlign: 'middle',
        width: '30%',
      },
      quickLinkList: {
        marginTop: spacing.s12,
        overflow: 'auto',
        padding: `0 ${spacing.s12}px`,
        textAlign: 'center',
        whiteSpace: 'nowrap',
        width: '100%',
      },
    } as const)
)

const QuickLinks = () => {
  const lang = useLang()
  const styles = useStyles()
  const { disableQuickLinkCards } = useConfig()

  // filter out quick links base on intent
  const quickLinks = useSelector(filteredQuickLinks)

  // loyalty is enabled with data
  if (disableQuickLinkCards || !quickLinks.length) return null

  // Note: Classname can be removed once retailer customization of icon are migrated to new system
  // currently needed: .quick-links.quicklink-icon
  return (
    <div data-test="quick-links" className="quick-links">
      <h2 className="screen-reader-text">{lang.quickLinks}</h2>
      <ul css={styles.quickLinkList} data-test="quicklinks-mobile">
        {quickLinks.map(quickLink => (
          <li
            key={`quick-link-key-${quickLink.id}`}
            css={styles.quickLinkItem}
            aria-label={`Quick Link - ${quickLink.name}`}
          >
            <QuickLinkItem quickLink={quickLink} />
          </li>
        ))}
      </ul>
    </div>
  )
}

export default QuickLinks
