import { getUniquePlacements, useLayout } from '@app/react/common/hooks/useLayout'
import { BIA_COLLECTION_ID } from '@app/services/collections/constants'
import { useSinglePlacementQuery } from '@app/services/placements/hooks'
import { PlacementKeys } from '@app/react/moduleMapping'
import { Layout } from '@app/types/layouts/placement'

interface ComposableHomeDesktopViewProps {
  layout: Layout
}

type SeenPlacementsIndexes = {
  [key in PlacementKeys]: number
}

const ComposableHomeDesktopView = ({ layout }: ComposableHomeDesktopViewProps) => {
  // Get unique sections to determine what data to fetch
  const uniquePlacements = getUniquePlacements(layout.placements)
  const sections = [...layout.placements]

  // Start data fetching based on sections
  // Only fetch data if the type exists in the layout
  const { data: collectionsData } = useSinglePlacementQuery('homepage_collections', {
    enabled: !!uniquePlacements.includes('itemList'),
  })

  // const { data: heroData } = useSinglePlacementQuery('homepage_hero', {
  //   enabled: !!uniquePlacements.includes('homepage_hero'),
  // })

  // const { data: homepageBannersData } = useSinglePlacementQuery('homepage_banners', {
  //   enabled: !!uniquePlacements.includes('homepage_banners'),
  // })

  // Keep track of sections that have been seen already
  const placementIndexes: SeenPlacementsIndexes = uniquePlacements.reduce(
    (placements, currentkey) => {
      return {
        ...placements,
        [currentkey]: 0,
      }
    },
    {}
  )

  // Merge data with layout
  const layoutWithData = sections.map(section => {
    let newProps = section.props

    // TODO - Fix types
    if (
      section?.type === 'itemList' &&
      section?.itemList?.itemList?.dataQuery?.id === BIA_COLLECTION_ID
    ) {
      // buy it again
      newProps = { id: section?.itemList?.itemList?.dataQuery?.id }
    } else if (section.type === 'itemList') {
      const collection = collectionsData?.items[placementIndexes[section.type]]
      newProps = collection?.target?.collection
    } else if (section.type === 'homepage_hero') {
      // TODO - this is useless since the component doesn't accept props
      // newProps = heroData?.items[placementIndexes[section.type]] || {}
    } else if (section.type === 'homepage_banners') {
      // newProps = homepageBannersData?.items[placementIndexes[section.type]] || {}
    }

    // Bump the counter once the data has been retrieved
    placementIndexes[section.type] = placementIndexes[section.type] + 1
    return { ...section, props: newProps }
  })

  // Get the full layout
  const { data } = useLayout(layoutWithData)

  return <>{data}</>
}

export default ComposableHomeDesktopView
