import React from 'react'
import { createStyles } from '@app/react/common/hooks/createStyles'
import { spacing, responsive } from '@instacart/ids-core'
import AppLink from '@app/react/common/AppLink'
import { useLang } from '@app/services/config/hooks'
import { Tag } from '@app/types/tags'
import { useIsMobile } from '@app/react/common/hooks/useIsMobile'

const ICON_SIZE = '60px'
const MOBILE_ICON_SIZE = '40px'

const useStyles = createStyles(theme => ({
  goIcon: {
    marginLeft: spacing.s8,
    verticalAlign: 'middle',
  },
  goLink: {
    fontSize: theme.typography.bodySmall2.fontSize,
    verticalAlign: 'middle',
  },
  icon: {
    color: theme.colors.systemGrayscale00,
    flex: `0 0 ${ICON_SIZE}`,
    fontSize: '30px',
    height: ICON_SIZE,
    lineHeight: ICON_SIZE,
    textAlign: 'center',
    width: ICON_SIZE,
    [responsive.down('c')]: {
      flex: `0 0 ${MOBILE_ICON_SIZE}`,
      fontSize: '20px',
      height: MOBILE_ICON_SIZE,
      lineHeight: MOBILE_ICON_SIZE,
      width: MOBILE_ICON_SIZE,
    },
  },
  infoWrapper: {
    alignSelf: 'center',
    padding: spacing.s12,
    [responsive.down('c')]: {
      padding: 0,
      whiteSpace: 'nowrap',
    },
  },
  link: {
    display: 'flex',
    [responsive.down('c')]: {
      alignItems: 'center',
      flexDirection: 'column',
    },
  },
  name: {
    ...theme.typography.bodyMedium1,
    [responsive.down('c')]: {
      display: 'inline-block',
      padding: spacing.s8,
      textAlign: 'center',
      whiteSpace: 'normal',
    },
  },
  root: {
    alignItems: 'center',
    display: 'flex',
    margin: `${spacing.s12}px 0`,
    width: '270px',
    [responsive.down('c')]: {
      flex: '0 1 content',
      padding: spacing.s4,
    },
  },
  roundedIcon: {
    borderRadius: '50%',
  },
}))

const TagItem = ({ color, name, icon, id }: Tag) => {
  const styles = useStyles()
  const lang = useLang()
  const isMobile = useIsMobile()

  const backgroundColor = {
    backgroundColor: color,
  }

  const linkColor = {
    color,
  }

  return (
    <li css={styles.root}>
      <AppLink to={`/shop/categories?tags=${id}`} css={[styles.link, color && linkColor]}>
        <span
          className="tag-icon"
          css={[styles.icon, color && backgroundColor, styles.roundedIcon]}
        >
          <span className={`icon-${icon}`} aria-hidden="true" />
        </span>
        <span css={styles.infoWrapper}>
          <span data-test="tagitem-title" css={styles.name}>
            {name}
          </span>
          <br />
          {!isMobile && (
            <span css={styles.goLink}>
              {lang.shopNow} <span css={styles.goIcon} className="icon-go" aria-hidden="true" />
            </span>
          )}
        </span>
      </AppLink>
    </li>
  )
}

export default TagItem
