import {
  Children,
  PropsWithChildren,
  ReactElement,
  cloneElement,
  useEffect,
  useMemo,
  useState,
} from 'react'
import { createStyles } from '@app/react/common/hooks/createStyles'
import { useKeenSlider } from 'keen-slider/react'
import { useLangAria } from '@app/services/config/hooks'
import { ChevronLeftIcon, ChevronRightIcon, spacing } from '@instacart/ids-core'

const useStyles = createStyles(theme => ({
  activeDot: {
    backgroundColor: theme.colors.brandPrimaryRegular,
  },
  arrow: {
    cursor: 'pointer',
    fill: '#343538',
    height: 17,
    width: 17,
  },
  arrowButton: {
    WebkitTransform: 'translateY(-50%)',
    alignItems: 'center',
    backgroundColor: '#ffffff',
    borderRadius: '20px',
    boxShadow: '2px 2px 9px -2px #00000040',
    display: 'flex',
    height: '40px',
    justifyContent: 'center',
    position: 'absolute',
    top: '50%',
    transform: 'translateY(-50%)',
    width: '40px',
  },
  arrowDisabled: {
    fill: 'rgba(255, 255, 255, 0.5)',
  },
  arrowLeft: {
    left: '-20px',
    svg: {
      marginLeft: spacing.s4,
    },
  },
  arrowRight: {
    left: 'auto',
    right: '-20px',
    svg: {
      marginLeft: -spacing.s4,
    },
  },
  container: {
    '> *': {
      minHeight: '100%',
      overflow: 'hidden',
      position: 'relative',
      width: '100%',
    },
    WebkitTapHighlightColor: 'transparent',
    WebkitTouchCollout: 'none',
    display: 'flex',
    overflow: 'hidden',
    position: 'relative',
    touchAction: 'pan-y',
    userSelect: 'none',
  },
  content: {
    position: 'relative',
  },
  controls: {
    position: 'relative',
  },
  dot: {
    backgroundColor: theme.colors.systemGrayscale30,
    borderRadius: '50%',
    display: 'inline-block',
    height: 8,
    margin: 5,
    transition: 'background-color 0.3s',
    width: 8,
  },
  dotLarge: {
    height: 12,
    margin: `${spacing.s16}px ${spacing.s8}px`,
    width: 12,
  },
  dots: {
    '> *': {
      display: 'inline-block',
    },
    textAlign: 'center',
  },
  pause: {
    left: 2,
    position: 'absolute',
    top: 15,
    width: 16,
  },
  roundedCorners: {
    borderRadius: theme.radius.r8,
  },
}))

export interface SliderProps {
  ariaName?: string
  autoplay?: boolean
  autoplayDelay?: number
  largeControls?: boolean
  onChange?: (index: number) => void
  roundedCorners?: boolean
  showArrowControls?: boolean
  showAutoplayControls?: boolean
}

const Slider = ({
  ariaName,
  autoplay = false,
  autoplayDelay = 5,
  children,
  largeControls = false,
  onChange,
  roundedCorners = false,
  showArrowControls = false,
  showAutoplayControls = false,
}: PropsWithChildren<SliderProps>) => {
  const langAria = useLangAria()
  const styles = useStyles()
  const [currentIndex, setCurrentIndex] = useState(0)
  const [pause, setPause] = useState(false)
  const [forcePause, setForcePause] = useState(false)

  const autoplayProps = autoplay
    ? {
        dragEnd: () => {
          setPause(false)
        },
        dragStart: () => {
          setPause(true)
        },
        duration: 1000,
        loop: true,
      }
    : {}

  const [ref, slider] = useKeenSlider<HTMLDivElement>({
    ...autoplayProps,
    // This is a hacky fix to force KeenSlider to return a new updated 'ref' and 'slider' when the number of slides change by passing in different options to pass a deep comparison
    // https://github.com/rcbyr/keen-slider/blob/118deaaf49885374ce512ecd9c71ecec3dad7174/src/react.js#L25
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore is not assignable to type
    _fix: Children.count(children),
    afterChange: ({ details }) => {
      const { absoluteSlide: index } = details()
      const slide = ref.current?.children[index]
      if (slide) {
        slide.removeAttribute('aria-hidden')
        slide.removeAttribute('tabindex')
      }
    },
    slideChanged: ({ details }) => {
      const { relativeSlide } = details()
      setCurrentIndex(relativeSlide || 0)
      ;[...(ref.current?.children ?? [])].forEach(slide => {
        slide.setAttribute('aria-hidden', 'true')
        slide.setAttribute('tabindex', '-1')
      })
    },
    slides: (): NodeListOf<Element> => (ref.current?.children as unknown) as NodeListOf<Element>,
  })

  useEffect(() => {
    slider && slider.refresh()
  }, [children, slider])

  useEffect(() => {
    const pauseAutoplay = () => {
      setPause(true)
    }
    const unpauseAutoplay = () => {
      setPause(false)
    }

    const currentRef = ref.current

    if (autoplay) {
      currentRef?.addEventListener('mouseover', pauseAutoplay)
      currentRef?.addEventListener('mouseout', unpauseAutoplay)
    }

    return () => {
      currentRef?.removeEventListener('mouseover', pauseAutoplay)
      currentRef?.removeEventListener('mouseout', unpauseAutoplay)
    }
  }, [autoplay, ref])

  useEffect(() => {
    if (!autoplay || pause || forcePause) return
    const timer = setTimeout(() => {
      if (slider && !pause && !forcePause) {
        if (currentIndex === slider.details().size - 1) slider.moveToSlideRelative(0)
        else slider.next()
      }
    }, 1000 * autoplayDelay)

    return () => {
      clearInterval(timer)
    }
  }, [autoplay, autoplayDelay, currentIndex, forcePause, pause, slider])

  const clonedChildren = useMemo(
    () =>
      Children.map(children, (child, index) =>
        cloneElement(child as ReactElement, {
          'aria-label': langAria.slideNumberOfCount
            // eslint-disable-next-line no-template-curly-in-string
            .replace('${number}', (index + 1).toString())
            // eslint-disable-next-line no-template-curly-in-string
            .replace('${count}', Children.count(children).toString()),
          role: 'group',
        })
      ),
    [children, langAria]
  )

  const slideCount = slider ? slider.details().size : 0

  const indexes = useMemo(() => [...Array(slideCount).keys()], [slideCount])

  useEffect(() => {
    onChange?.(currentIndex)
  }, [currentIndex, onChange])

  return (
    <section aria-label={(ariaName ? `${ariaName}, ` : '') + langAria.slider}>
      <div css={styles.content}>
        <p className="screen-reader-text">{langAria.sliderInstructions}</p>
        <div
          css={[styles.container, roundedCorners && styles.roundedCorners]}
          ref={ref}
          data-test="slider-slides"
        >
          {clonedChildren}
        </div>
        {showArrowControls && slider && (
          <>
            <button
              aria-label={langAria.sliderPreviousSlide}
              css={[styles.arrowButton, styles.arrowLeft]}
              onClick={e => {
                e.stopPropagation()
                slider.prev()
              }}
            >
              <ChevronLeftIcon css={styles.arrow} />
            </button>
            <button
              aria-label={langAria.sliderNextSlide}
              css={[styles.arrowButton, styles.arrowRight]}
              onClick={e => {
                e.stopPropagation()
                slider.next()
              }}
            >
              <ChevronRightIcon css={styles.arrow} />
            </button>
          </>
        )}
      </div>
      <div css={styles.controls}>
        {autoplay && showAutoplayControls && (
          <button
            aria-label={forcePause ? langAria.sliderPlay : langAria.sliderPause}
            css={styles.pause}
            onClick={() => {
              setForcePause(!forcePause)
            }}
          >
            {forcePause && <span className="icon-play" />}
            {!forcePause && <span className="icon-pause" />}
          </button>
        )}
        {indexes && (
          <ul css={styles.dots}>
            {indexes.map(index => {
              const isActive = currentIndex === index
              return (
                <li key={index}>
                  <button
                    css={[
                      styles.dot,
                      largeControls && styles.dotLarge,
                      isActive && styles.activeDot,
                    ]}
                    onClick={() => {
                      slider.moveToSlideRelative(index)
                    }}
                    aria-label={
                      /* eslint-disable-next-line no-template-curly-in-string */
                      langAria.goToSlide.replace('${index}', (index + 1).toString())
                    }
                    aria-current={isActive ? 'true' : undefined}
                    data-test="slider-dot"
                  />
                </li>
              )
            })}
          </ul>
        )}
      </div>
    </section>
  )
}

export default Slider
