import { ElementType } from 'react'
import { PlacementTypeEnum } from '@app/types/layouts/placement'
import BannerPlacement from './components/placements/banner/BannerPlacement'
import ItemListPlacement from './components/placements/itemList/ItemListPlacement'
import CardListPlacement from './components/placements/cardList/CardListPlacement'
import UnataFeaturePlacement from './components/placements/unataFeature/UnataFeaturePlacement'

export interface PlacementMapping {
  [key: string]: ElementType
}

const placements: PlacementMapping = {
  [PlacementTypeEnum.Banner]: BannerPlacement,
  [PlacementTypeEnum.ItemList]: ItemListPlacement,
  [PlacementTypeEnum.CardList]: CardListPlacement,
  [PlacementTypeEnum.UnataFeature]: UnataFeaturePlacement,
}

export type PlacementKeys = keyof typeof placements

export const getModule = (currentModule: string) => {
  // TODO - may change this to a switch statement at some point to support multiple components re-using the same container
  // We should also add prop validation in here
  return placements[currentModule]
}

export default placements
