import { useEffect, useMemo } from 'react'
import Slider from '@app/react/components/slider'
import { Placement } from '@app/types/placements'
import { imageTransform, analytics } from '@instacart/enterprise-services'
import { createStyles } from '@app/react/common/hooks/createStyles'
import { useIsMobile } from '@app/react/common/hooks/useIsMobile'
import { logImpression, parseContentHref } from '@app/services/canvas/utils'
import { useConfig, useLangAria } from '@app/services/config/hooks'
import { logPlacementImpression } from '@app/services/placements/analytics'
import { useSinglePlacementQuery } from '@app/services/placements/hooks'
import { escapeImageUrl } from '@app/services/util/utils'
import { eventEmitter, EVENT_TYPES } from '@app/utils/eventEmitter'
import { spacing } from '@instacart/ids-core'
import { useExpressHeroBannerPlacement } from '@app/services/express/hooks'
import { ANALYTICS_TYPE_HERO_BANNER } from '@app/services/placements/constants'

const handleClick = (banner: Placement, index: number, isMobile: boolean) => {
  const category = isMobile ? 'mobile homepage hero' : 'homepage hero'
  analytics.log({
    data: {
      action: 'click',
      category,
      label: index,
      value: banner.id,
    },
    event: 'event',
  })

  eventEmitter.emit(EVENT_TYPES.LOG_PROMOTED_PLACEMENT, {
    location: 'Homepage Hero',
    placement: banner,
    position: index,
  })
}

const getBannerAlt = (banner: Placement) => {
  const defaultAlt = banner.media.alt_text ?? undefined
  switch (banner.target.type) {
    case 'collection':
      return banner.target.collection.description ?? defaultAlt
    case 'product':
      return banner.target.product.description ?? defaultAlt
    default:
      return defaultAlt
  }
}

const getBannerImage = (banner: Placement) =>
  // express hero banner uses locally served image
  banner.id === 'express_hero_banner'
    ? banner.media.image
    : imageTransform(escapeImageUrl(banner.media.image || ''))

const useStyles = createStyles(theme => ({
  loader: {
    backgroundColor: theme.colors.systemGrayscale20,
    height: 270,
  },
  mobile: {
    paddingLeft: spacing.s16,
    paddingRight: spacing.s16,
  },
  singleBanner: {
    height: 270,
    width: '100%',
  },
  singleBannerImage: {
    height: '100%',
    objectFit: 'cover' as 'cover',
    objectPosition: '50% 50%',
  },
  singleBannerLink: {
    display: 'flex',
    flexDirection: 'column' as 'column',
    height: 270,
    overflow: 'hidden',
    width: '100%',
  },
  singleBannerMobile: {
    '& > a': {
      '& > img': {
        width: '100%',
      },

      height: '100%',
    },
    height: '100%',
  },
  slider: {
    margin: `${spacing.s32}px auto`,
    paddingLeft: spacing.s32,
    paddingRight: spacing.s32,
    width: '100%',
  },
  sliderBanner: {
    display: 'block',
    margin: '0 auto',
    maxWidth: '100%',
  },
  sliderNarrowWidth: {
    maxWidth: 1306,
  },
}))

const HeroBanner = () => {
  const config = useConfig()
  const styles = useStyles()
  const langAria = useLangAria()
  const isMobile = useIsMobile()
  const expressHeroBanner = useExpressHeroBannerPlacement()

  const sliderEnabled = config.homepageHeroBannerSlider.enabled
  const sliderNarrowWidth = !config.homepageHeroBannerSlider.fullWidth

  const { data } = useSinglePlacementQuery(isMobile ? 'mobile_homepage_hero' : 'homepage_hero')

  const items = data?.items

  // if there is an expressHeroBanner, then add it as the first banner
  const banners = useMemo(() => {
    if (!items) return items
    if (expressHeroBanner) return [expressHeroBanner, ...items]
    return items
  }, [items, expressHeroBanner])

  useEffect(() => {
    if (!sliderEnabled && banners && banners.length > 0) {
      logImpression(banners[0])
    }
  }, [banners, sliderEnabled])

  const slicedBanners = useMemo(() => {
    if (banners && sliderEnabled)
      return banners.slice(0, config.homepageHeroBannerSlider.maxBanners)
    return null
  }, [sliderEnabled, banners, config])

  useEffect(() => {
    logPlacementImpression({
      location: 'homepage',
      placements: slicedBanners,
      type: ANALYTICS_TYPE_HERO_BANNER,
    })
  }, [slicedBanners])

  if (!banners) {
    return (
      <div
        css={styles.loader}
        className="loading-shimmer"
        role="alert"
        aria-busy="true"
        aria-label={langAria.loadingImage}
      />
    )
  }

  if (!sliderEnabled) {
    const banner = banners[0]

    if (!banner) return null

    return (
      <div
        css={[styles.singleBanner, isMobile && styles.singleBannerMobile]}
        data-test={`hero-content`}
      >
        {/* eslint-disable-next-line jsx-a11y/no-noninteractive-element-interactions, jsx-a11y/click-events-have-key-events */}
        <a
          role="banner"
          css={styles.singleBannerLink}
          {...{
            target: banner.target?.openIn === 'new_tab' ? '_blank' : undefined,
            ...parseContentHref(banner),
          }}
          onClick={() => handleClick(banner, 0, isMobile)}
          data-test={`content-banner`}
        >
          <img
            css={styles.singleBannerImage}
            src={getBannerImage(banner)}
            alt={getBannerAlt(banner)}
          />
        </a>
      </div>
    )
  }

  return (
    <div
      css={[
        styles.slider,
        isMobile && styles.mobile,
        sliderNarrowWidth && styles.sliderNarrowWidth,
      ]}
    >
      <Slider
        autoplay={config.homepageHeroBannerSlider.autoplayEnabled}
        autoplayDelay={config.homepageHeroBannerSlider.autoplayDelay}
        showArrowControls={config.homepageHeroBannerSlider.showArrowControls}
        showAutoplayControls={config.homepageHeroBannerSlider.showAutoplayControls}
        roundedCorners
        largeControls
        onChange={index => {
          const banner = banners[index]
          if (banner) logImpression(banners[index])
        }}
      >
        {slicedBanners &&
          slicedBanners.map((banner, index) => (
            // eslint-disable-next-line jsx-a11y/click-events-have-key-events, jsx-a11y/no-static-element-interactions
            <a
              data-test={`content-banner-${banner.id}`}
              {...{
                target: banner.target?.openIn === 'new_tab' ? '_blank' : undefined,
                ...parseContentHref(banner),
              }}
              onClick={() => handleClick(banner, index, isMobile)}
              key={banner.id}
            >
              {banner.media.image && (
                <img
                  css={styles.sliderBanner}
                  src={getBannerImage(banner)}
                  alt={getBannerAlt(banner)}
                />
              )}
            </a>
          ))}
      </Slider>
    </div>
  )
}

export default HeroBanner
