import { spacing } from '@instacart/ids-core'
import { Placement } from '@app/types/placements'
import { logImpression } from '@app/services/canvas/utils'
import { useConfig, useLang } from '@app/services/config/hooks'
import { createStyles } from '@app/react/common/hooks/createStyles'
import Slider from '@app/react/components/slider'
import Banners from './Banners'
import BannersLayout from './BannersLayout'

const getStyles = createStyles(() => ({
  slider: {
    margin: spacing.s16,
  },
}))

type Props = {
  banners: Placement[]
  handleClick: (banner: Placement, index: number) => void
}

const MobileHomepageBanners = ({ banners, handleClick }: Props) => {
  const styles = getStyles()
  const lang = useLang()
  const config = useConfig()

  if (config.enableHomepageBannerSlider) {
    return (
      <Slider
        css={styles.slider}
        onChange={index => {
          logImpression(banners[index])
        }}
        ariaName={lang.shopHomepageBannersHeader}
      >
        <Banners banners={banners} onBannerClick={handleClick} />
      </Slider>
    )
  }

  return (
    <BannersLayout>
      <Banners banners={banners} onBannerClick={handleClick} />
    </BannersLayout>
  )
}

export default MobileHomepageBanners
