import { MouseEventHandler } from 'react'
import { Placement } from '@app/types/placements'
import { parseContentHref } from '@app/services/canvas/utils'
import { useIsMobile } from '@app/react/common/hooks/useIsMobile'
import { createStyles } from '@app/react/common/hooks/createStyles'
import BannerImage from './BannerImage'
import BannerLayout from './BannerLayout'

const getStyles = createStyles(
  theme =>
    ({
      bannerDesktop: {
        ':last-child': {
          marginRight: 0,
        },
        backgroundPosition: 'center center',
        backgroundRepeat: 'no-repeat',
        display: 'inline-block',
        height: '202px',
        marginRight: '30px',
        overflow: 'hidden',
        verticalAlign: 'top',
        width: '347px',
      },
      bannerMobile: {
        display: 'block',
        height: 0,
        overflow: 'hidden',
        paddingTop: 'calc(203%/347*100)', // I know...
        position: 'relative',
        width: '100%',
      },
      root: {
        backgroundSize: 'cover',
        borderRadius: theme.radius.r4,
      },
    } as const)
)

type BannerProps = {
  banner: Placement
  onClick: MouseEventHandler
}

const Banner = ({ banner, onClick }: BannerProps) => {
  const isMobile = useIsMobile()
  const styles = getStyles()

  const BannerInstance = (
    // TODO: replace anchor tag with AppLink
    /* eslint-disable-next-line jsx-a11y/click-events-have-key-events, jsx-a11y/no-static-element-interactions */
    <a
      css={[styles.root, isMobile ? styles.bannerMobile : styles.bannerDesktop]}
      data-test={`content-banner-${banner.id}`}
      onClick={onClick}
      rel="noreferrer"
      {...{
        target: banner.target?.openIn === 'new_tab' ? '_blank' : undefined,
        ...parseContentHref(banner),
      }}
    >
      <BannerImage banner={banner} />
    </a>
  )

  if (isMobile) return <BannerLayout>{BannerInstance}</BannerLayout>

  return BannerInstance
}

export default Banner
