import { createStyles } from '@app/react/common/hooks/createStyles'
import { useLang } from '@app/services/config/hooks'
import loadAssetImage from '@app/utils/loadAssetImage'
import AppLink from '@app/react/common/AppLink'
import { useTags } from '@app/services/tags/hooks'
import { spacing, responsive } from '@instacart/ids-core'
import { useIsMobile } from '@app/react/common/hooks/useIsMobile'
import Section from '@app/react/common/Section'
import TagItem from './TagItem'

interface WaysToShopProps {
  showMoreLink?: boolean
}

const useStyles = createStyles(theme => ({
  container: {
    display: 'flex',
    [responsive.down('c')]: {
      flexDirection: 'column',
      overflowX: 'scroll',
      padding: 0,
    },
  },
  goIcon: {
    marginLeft: spacing.s8,
    verticalAlign: 'middle',
  },
  goLink: {
    color: theme.colors.brandPrimaryRegular,
    fontSize: theme.typography.bodySmall2.fontSize,
    fontStyle: 'italic',
    textDecoration: 'underline',
    verticalAlign: 'middle',
  },
  header: {
    ...theme.typography.bodyLarge2,
    margin: `${spacing.s8}px 0`,

    strong: {
      fontSize: '26px',
      fontWeight: theme.typography.subtitleMedium.fontWeight,
    },
  },
  icon: {
    fontSize: '29px',
    lineHeight: 1,
  },
  labelWrapper: {
    alignItems: 'center',
    backgroundImage: `url(${loadAssetImage('images/quickshop-arrow.svg')})`,
    backgroundPosition: '100%',
    backgroundRepeat: 'no-repeat',
    backgroundSize: 'cover',
    display: 'flex',
    flex: '1 0 auto',
    marginRight: spacing.s32,
    padding: `${spacing.s24}px ${spacing.s16}px`,
    width: '247px',
  },
  shopByTitle: {
    alignSelf: 'center',
    flexDirection: 'column',
    fontSize: theme.typography.subtitleLarge.fontSize,
    fontWeight: theme.typography.subtitleLarge.fontWeight,
    paddingBottom: spacing.s12,
  },
  tagsWrapper: {
    display: 'flex',
    flexWrap: 'wrap',
    width: '100%',
    [responsive.down('c')]: {
      alignItems: 'baseline',
      flexWrap: 'nowrap',
      overflowX: 'scroll',
      position: 'relative',
    },
  },
}))

const WaysToShop = ({ showMoreLink }: WaysToShopProps) => {
  const styles = useStyles()
  const lang = useLang()
  const tags = useTags('quickshop')
  const isMobile = useIsMobile()

  return (
    <Section boxed={!isMobile} css={styles.container}>
      {!isMobile ? (
        <div css={styles.labelWrapper}>
          <div className="label">
            <span css={styles.icon} className="icon icon-cart" aria-hidden="true" />
            <h2 css={styles.header}>
              {lang.exploreMore}
              <br />
              <strong>{lang.waysToShop}</strong>
            </h2>
            {showMoreLink && (
              <AppLink
                css={styles.goLink}
                to="/shop"
                aria-label={`${lang.seeMore} ${lang.waysToShop}`}
              >
                {lang.seeMore}
                <span css={styles.goIcon} className="icon-go" aria-hidden="true" />
              </AppLink>
            )}
          </div>
        </div>
      ) : (
        <h2 css={styles.shopByTitle}>{lang.shopBy}</h2>
      )}
      <ul data-test="waysToShop-tags" css={styles.tagsWrapper}>
        {tags?.map(tag => tag && <TagItem key={tag.id} {...tag} />)}
      </ul>
    </Section>
  )
}

export default WaysToShop
