import { FC } from 'react'
import { spacing } from '@instacart/ids-core'
import { createStyles } from '@app/react/common/hooks/createStyles'
import { useIsMobile } from '@app/react/common/hooks/useIsMobile'

const getStyles = createStyles(
  () =>
    ({
      bannersDesktop: {
        display: 'block',
        height: '203px',
        margin: `${spacing.s16}px 0`,
        overflow: 'hidden',
        position: 'relative',
        textAlign: 'center',
        width: '100%',
      },
      bannersMobile: {
        padding: `0 ${spacing.s16}px`,
      },
    } as const)
)

const BannersLayout: FC<unknown> = ({ children }) => {
  const isMobile = useIsMobile()
  const styles = getStyles()

  return (
    <div css={[isMobile ? styles.bannersMobile : styles.bannersDesktop]} role="group">
      {children}
    </div>
  )
}

export default BannersLayout
