import CategoryCard from '@app/react/components/categories/CategoryCard'
import { createStyles } from '@app/react/common/hooks/createStyles'
import { spacing } from '@instacart/ids-core'
import {
  useCategoriesLoadingState,
  useCategoriesFilteredByParent,
} from '@app/services/catalog/hooks'
import { useIsMobile } from '@app/react/common/hooks/useIsMobile'

import { generateAnimatingShimmerStyles } from '@app/react/ids/enterprise/molecules/LoadingLockups/bases/animations'

const useStyles = createStyles(theme => ({
  list: {
    display: 'flex',
    flexWrap: 'wrap',
    listStyleType: 'none',
    margin: `-${spacing.s32}px -${spacing.s32}px 0`,
    padding: 0,
  },
  listCentered: {
    justifyContent: 'center',
  },
  listItem: {
    margin: `${spacing.s32}px ${spacing.s16}px 0`,
    maxWidth: 249,
    width: '100%',
  },
  loadingState: {
    background: theme.colors.systemGrayscale10,
    borderRadius: theme.radius.r12,
    height: 230,
    width: '100%',
    ...generateAnimatingShimmerStyles(theme),
  },
  mobileList: {
    margin: `-${spacing.s16}px -${spacing.s8}px 0`,
  },
  mobileListItem: {
    margin: `${spacing.s16}px ${spacing.s8}px 0`,
    width: `calc(50% - ${spacing.s16}px)`,
  },
}))

interface Props {
  centered?: boolean
  filterByParentId?: string
  showChildren?: boolean
}

const CategoryCardCollection = ({
  centered = false,
  filterByParentId,
  showChildren = false,
}: Props) => {
  const styles = useStyles()

  const collection = useCategoriesFilteredByParent(filterByParentId)
  const isLoading = useCategoriesLoadingState()
  const isMobile = useIsMobile()

  // While categories load, display a loading state.
  // TODO - move this out in to a separate component.
  // Should be done when we make category cards more generic
  if (isLoading) {
    return (
      <ul
        css={[styles.list, centered && styles.listCentered, isMobile && styles.mobileList]}
        data-test="category-card-loading"
      >
        {Array.from(Array(12), (_, index) => (
          <li
            css={[styles.listItem, isMobile && styles.mobileListItem]}
            key={`category-loading-${index}`}
          >
            <div css={styles.loadingState} />
          </li>
        ))}
      </ul>
    )
  }

  if (!collection.length) {
    return null
  }

  return (
    <ul css={[styles.list, centered && styles.listCentered, isMobile && styles.mobileList]}>
      {collection.map(category => (
        <li css={[styles.listItem, isMobile && styles.mobileListItem]} key={category.id}>
          <CategoryCard category={category} showChildren={showChildren} />
        </li>
      ))}
    </ul>
  )
}

export default CategoryCardCollection
