import Section from '@app/react/common/Section'
import HomepageCollection from '@app/react/components/homepageCollections/HomepageCollection'
import { ItemList, ItemListVariants } from '@app/types/layouts/placement'
import type {
  Collection,
  HomepageCollection as HomepageCollectionType,
} from '@app/types/collection'
import { getVariant } from '../utils'

interface ItemListPlacementProps {
  itemList: ItemList
  id?: string
  props?: Collection | HomepageCollectionType
}

const ItemListPlacement = ({ id, itemList, props }: ItemListPlacementProps) => {
  const currentId = props?.id || id

  if (!currentId) {
    return null
  }

  return (
    <Section>
      {(() => {
        switch (getVariant(itemList)) {
          case ItemListVariants.ItemList:
            return <HomepageCollection id={currentId} />
          default:
            return null
        }
      })()}
    </Section>
  )
}

export default ItemListPlacement
