import { useCallback } from 'react'
import classNames from 'classnames'
import { spacing } from '@instacart/ids-core'
import { QuickLink } from '@app/types/config'
import { eventEmitter, EVENT_TYPES } from '@app/utils/eventEmitter'
import { createStyles } from '@app/react/common/hooks/createStyles'
import { styleVariables } from '@app/react/components/quickLinks/styles'

export interface QuickLinkItemProps {
  quickLink: QuickLink
}

const useStyles = createStyles(
  (theme, variables) => ({
    content: {
      '&:before': {
        content: '" "',
        display: 'inline-block',
        height: '40px',
        verticalAlign: 'middle',
        width: '0',
      },
      alignItems: 'center',
      backgroundColor: theme.colors[variables.labelBackgroundColor],
      borderRadius: `0 0 ${theme.radius.r8}px ${theme.radius.r8}px`,
      color: theme.colors[variables.labelColor],
      display: 'flex',
      fontSize: '12px',
      height: spacing.s48,
      overflow: 'hidden',
      padding: spacing.s4,
    },
    contentTitle: {
      marginLeft: '-2px',
      whiteSpace: 'normal',
      width: '100%',
      wordBreak: 'break-word',
    },
    icon: {
      display: 'block',
      fontSize: '45px',
      height: '100px',
      lineHeight: '100px',
    },
    iconText: {
      fontSize: '12px',
      left: 0,
      lineHeight: '20px',
      position: 'absolute',
      top: '65px',
      width: '100%',
    },
    quickLink: {
      backgroundColor: theme.colors[variables.containerBackgroundColor],
      border: '1px solid #ccc',
      borderRadius: `${theme.radius.r8}px`,
      color: theme.colors[variables.containerColor],
      position: 'relative',
      width: '100%',
    },
  }),
  styleVariables
)

const QuickLinkItem = ({ quickLink }: QuickLinkItemProps) => {
  // const { dotComModule } = useConfig()
  // const history = useHistory()
  const { iconText, id, name, icon } = quickLink
  const styles = useStyles()

  const handleClick = useCallback(() => {
    eventEmitter.emit(EVENT_TYPES.QUICK_LINK_CLICK, quickLink)
  }, [quickLink])

  return (
    <button css={styles.quickLink} onClick={handleClick} data-test={`quicklink-${id}`}>
      <span
        css={styles.icon}
        className={classNames('quicklink-icon', { [`icon-${icon}`]: icon })}
        aria-hidden="true"
      />

      {iconText && <span css={styles.iconText}>{iconText}</span>}

      <span css={styles.content} data-test={`quicklink-${id}-title`}>
        <span css={styles.contentTitle}>{name}</span>
      </span>
    </button>
  )
}

export default QuickLinkItem
