import { FC } from 'react'
import {
  CollectionLoader,
  CollectionLoaderHeader,
} from '@app/react/ids/enterprise/molecules/CollectionLoader'
import { CarouselLoader } from '@app/react/ids/enterprise/molecules/CarouselLoader'
import { useLangAria } from '@app/services/config/hooks'

export interface ProductCollectionLoaderProps {
  fullWidth?: boolean
}

const ProductCollectionLoader: FC<ProductCollectionLoaderProps> = ({ fullWidth }) => {
  const langAria = useLangAria()

  return (
    <CollectionLoader accessibleLabel={langAria.loading} fullWidth={fullWidth}>
      <CollectionLoaderHeader />
      <CarouselLoader />
    </CollectionLoader>
  )
}

export default ProductCollectionLoader
