import request from '@app/utils/request'
import { Collection } from '@app/types/collection'
import { COLLECTION_LIMIT_MULTIPLIER } from './constants'

export const getOne = (collectionId: string | number, limit: number) =>
  request<Collection>(`/collections/${collectionId}`, {
    querystring: {
      limit: limit * COLLECTION_LIMIT_MULTIPLIER,
    },
  })
