import { HTMLProps } from 'react'
import { createStyles } from '@app/react/common/hooks/createStyles'

interface CustomDivElementProps {
  backgroundColor?: string
}

const useStyles = createStyles(theme => ({
  root: {
    alignItems: 'center',
    backgroundColor: theme.colors.brandPrimaryRegular,
    color: '#FFFFFF',
    display: 'flex',
    height: 200,
    justifyContent: 'center',
  },
}))

/*
 * This is a temporary component used in the composable storefront POC to prove out API driven props.
 * Long term, we will probably only support layout props such as height, width, fullWidth,
 * custom css classes and a unique id. Colors and any styling will be removed.
 */
const CustomDivElement = ({
  backgroundColor,
  children,
  height,
}: CustomDivElementProps & HTMLProps<HTMLDivElement>) => {
  // TODO - add boxed variant
  const styles = useStyles()
  let additionalStyles = {}

  if (height) {
    additionalStyles = {
      ...additionalStyles,
      height,
    }
  }

  if (backgroundColor) {
    additionalStyles = {
      ...additionalStyles,
      backgroundColor,
    }
  }

  return (
    <div css={[styles.root, additionalStyles]}>
      <div>{children}</div>
    </div>
  )
}

export default CustomDivElement
