import { keyframes } from '@emotion/core'
import { addAlphaToHex, Theme } from '@instacart/ids-core'

/*
 * Copied from the IDS core library for now. Should export/share this eventually.
 * https://github.com/instacart/instacart-design-system-web/blob/master/packages/core/src/molecules/LoadingLockups/bases/animation.ts
 */

const SHIMMER_WIDTH_PERCENTAGE = 55
const TRANSLATE_X_START = -100
// Using translate, our percentage-based positioning is no longer relative to
// the parent, but rather, the element being translated. Because we're building
// on top of a percentage-based value already, we find the reciprocal in order
// to regain context of parent width to calculate end x value.
// i.e., animated elem is 55% of parent, therefore parent width is 1 / 55%
const TRANSLATE_X_END = (1 / (SHIMMER_WIDTH_PERCENTAGE / 100)) * 100
const ANIMATION_DURATION_MS = 1000

export const generateAnimatingShimmerStyles = (theme: Theme) => {
  const gradientColor = theme.colors.systemGrayscale00
  const gradientStart = addAlphaToHex(gradientColor, 0)
  const gradientMid = addAlphaToHex(gradientColor, 0.75)
  const gradientEnd = gradientStart

  const shimmerSweep = keyframes`
      0% {
        transform: translate3d(${TRANSLATE_X_START}%,0,0);
      }
      100% {
        transform: translate3d(${TRANSLATE_X_END}%,0,0);
      }
    `

  return {
    '&:before': {
      animationDuration: `${ANIMATION_DURATION_MS}ms`,
      animationIterationCount: 'infinite',
      animationName: shimmerSweep,
      animationTimingFunction: 'ease-in-out',
      background: `linear-gradient(to right, ${gradientStart}, ${gradientMid}, ${gradientEnd})`,
      content: "''",
      height: '100%',
      left: 0,
      position: 'absolute',
      width: `${SHIMMER_WIDTH_PERCENTAGE}%`,
    },
    overflow: 'hidden',
    position: 'relative',
  } as const
}
