import { CollectionId } from '@app/types/collection'
import { useQuery, UseQueryOptions } from 'react-query'
import { PromiseType } from 'utility-types'
import { getOne } from './api'
import { PRODUCT_LIMIT, SINGLE_COLLECTION_QUERY_KEY } from './constants'

/**
 * Query single collection by id
 */
export const useCollectionQuery = (
  collectionId: CollectionId,
  limit: number = PRODUCT_LIMIT,
  configOptions?: UseQueryOptions<PromiseType<ReturnType<typeof getOne>>>
) => {
  return useQuery<PromiseType<ReturnType<typeof getOne>>>(
    [SINGLE_COLLECTION_QUERY_KEY, { collectionId }],
    () => getOne(collectionId, limit),
    configOptions
  )
}
