import { useMemo } from 'react'
import { getModule } from '@app/react/moduleMapping'
import { Placement } from '@app/types/layouts/placement'

export const useLayout = (placements: Placement[]) => {
  const mappedPlacements = useMemo(() => {
    if (!placements) {
      return []
    }

    return (
      placements
        .map(placement => {
          // Try to find module against placement list
          const Module = getModule(placement.type)

          // If it can't be found, return nothing
          if (!Module) {
            return null
          }

          // TODO - add pass props to the placement
          return <Module key={`${placement.type}-${placement.id}`} {...placement} />
        })
        .filter(placement => placement) ?? [] // Filter out components that can't be found
    )
  }, [placements])

  return { data: mappedPlacements }
}

export const getUniquePlacements = (placements: Placement[] = []) => {
  const sectionTypes = placements.map(placement => placement.type).filter(type => type)
  return [...new Set(sectionTypes)]
}
