import { ReactNode } from 'react'
import { spacing } from '@instacart/ids-core'
import { createStyles } from '@app/react/common/hooks/createStyles'
import { useIsMobile } from '@app/react/common/hooks/useIsMobile'

export interface SectionProps {
  boxed?: boolean
  children: ReactNode
}

const useStyles = createStyles(() => ({
  boxed: {
    marginLeft: 'auto',
    marginRight: 'auto',
    maxWidth: 1154,
    width: '100%',
  },
  mobileRoot: {
    marginBottom: spacing.s16,
    marginTop: spacing.s16,
    padding: `0 ${spacing.s16}px`,
  },
  root: {
    marginBottom: spacing.s32,
    marginTop: spacing.s32,
    padding: `0 ${spacing.s32}px`,
  },
}))

const Section = ({ boxed, children, ...rest }: SectionProps) => {
  // TODO - update styles to match designs
  const styles = useStyles()
  const isMobile = useIsMobile()

  return (
    <section css={[styles.root, boxed && styles.boxed, isMobile && styles.mobileRoot]} {...rest}>
      {children}
    </section>
  )
}

export default Section
